import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import { Http } from '@angular/http';
import * as $ from 'jquery';
import {Constants} from "../../../../../config/Constants";
import { LoginPage } from '../../login/login';
import { ExplorePage } from '../../explore/explore';


/**
 * Generated class for the ConfirmCadPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-confirm-cad',
    templateUrl: 'confirm-cad.html',
})
export class ConfirmCadPage {
    
    public seisdigitos:boolean = false;
    public constants = Constants;

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public http: Http,
                private alertCtrl: AlertController
    ) {}

    /**
     * Funo utilizada para pular a confirmao do cadastro
     */
    
    public skipCad = (() => {
        /* Pula a confirmao do cadastro */
        let alert = this.alertCtrl.create({
            title: 'Ateno!',
            subTitle: 'Autenticao pendente. Valide seu email para ter acesso a todo nosso app.',
            enableBackdropDismiss: false,
            buttons: [
                {
                    text: 'Ok',
                    handler: ( ) =>{
                        this.navCtrl.setRoot(ExplorePage);
                    }
                }]
        });
        alert.present();
    });
    //funo verifica o codiga com a existencia de 6 digitos 
    public autenticaCod = (( code:any) => {

            //desabilita o input 
            //this.seisdigitos=true;

            //realiza o ajax enviando como parametro o cdigo de entrada "data"
            this.http.post(this.constants.api_path+'cadastro/autenticacao',$.param({codigo:code})).subscribe(data => {
                const response = (data as any);
                const objeto_retorno = JSON.parse(response._body);
                
                //console.log(response._body);
                //retorna o cdigo
                if(objeto_retorno === true){

                    //modal de confirmao para o usuario
                    let alert = this.alertCtrl.create({
                        title: 'Parabns',
                        subTitle: 'Parabns, voc realizou o cadastro em nosso sistema. Voc ser logado automticamente.',
                        enableBackdropDismiss: false,
                        buttons: [
                            {
                                text: 'Ok',
                                handler: ( ) =>{
                                    this.navCtrl.setRoot(ExplorePage);
                                }
                            }]
                    });
                    alert.present();
                }else{

                    //modal de cdigo invalido
                    let alert =this.alertCtrl.create({
                        title: 'Autenticao',
                        subTitle: 'Cdigo incorreto',
                        buttons: ['Ok']
                    });
                    alert.present();
                }

            }, error => {

                //modal de erro na autenticao
                let alert =this.alertCtrl.create({
                    title: 'Autenticao',
                    subTitle: 'Erro na autenticao',
                    buttons: ['Ok']
                });
                alert.present();
                console.log(error);
            });

    });

}



