import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, MenuController, AlertController} from 'ionic-angular';
import {FeedPage} from "../feed/feed";
import {ExplorePage} from "../explore/explore";
import * as $ from 'jquery';
import {LoginPage} from "../login/login";

/**
 * Generated class for the SystemTabsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-system-tabs',
    templateUrl: 'system-tabs.html',
})
export class SystemTabsPage {

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public menuCtrl: MenuController,
                private alertCtrl: AlertController
    ) {}

    public pageHome: any = FeedPage;
    public pageExplore: any = ExplorePage;
    public pageBag: any = null;
    public pageNotification: any = null;

    /**
     * Funo utilizada para realizar o Scroll at o topo da pagina, quando clicar na "tab" que j est ativa
     * @type {(event) => any} - Infos of pointer click event */
    public scrollTopContent = ((event) => {
        if ($(event.target).closest('.tab-button[aria-selected="true"]').length > 0) {
            $('.scroll-content').stop().animate({'scrollTop': 0}, 300)
        }
    });

    /* Fim da funo utilizada para o scroll at o topo da pagina */

    /**
     * Funo utilizada para abrir o menu de opes */
    public openMenu = (() =>{
        this.menuCtrl.open();
    }); /* Fim da funo utilizada para abrir o menu de opes */

    /**
     * Funo utilizada para realizar o Logout do usurio */
    public doLogout = (() => {

        let alert = this.alertCtrl.create({
            title: 'Logout',
            subTitle: 'Deseja realmente realizar o Logout do aplicativo?',
            enableBackdropDismiss: false,
            buttons: [
            {
                text: 'No',
                handler: () => {
                }
            },
            {
                text: 'Sim',
                handler: () => {
                    this.navCtrl.setRoot(LoginPage);
                } /* Hander of "OK" button */
            }]
        });
        alert.present();

    });/* Fim da funo utilizada para realizar o logout do usurio */

}



