import {Component} from '@angular/core';
import {Http} from "@angular/http";
import {IonicPage, NavController, NavParams, ToastController} from 'ionic-angular';
import {Constants} from "../../../../config/Constants";
import {SlidesPage} from "../slides/slides";
import {Step1Page} from "../cadastro/step1/step1";
import {SystemTabsPage} from "../system-tabs/system-tabs";
import {ForgotPass_1Page} from "../forgot-pass/forgot-pass-1/forgot-pass-1";
import * as $ from "jquery";
import {ConfirmCadPage} from "../cadastro/confirm-cad/confirm-cad";
import {Storage} from '@ionic/storage';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-login',
    templateUrl: 'login.html',
})
export class LoginPage {

    public constants = Constants;
    public login = {};
    public logindata = {
        user_login: "",
        user_pass: ""
    };

    public teste = "";

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public toastCtrl: ToastController,
                public http: Http,
                public storage: Storage) { /* Init of constructor of class */
    }

    /** Fim do constructor da classe */

    /**
     * Funo utilizada para redirecionar o usurio novamente  pagina de slides */
    public goBackSlides = (() => {
        this.navCtrl.push(SlidesPage);
    });
    /* Fim da funo de redirecionamento para os slides */

    /**
     * Funo utilizada para redirecionar o usurio  pagina de realizar o cadastro */
    public goToCadastro = (() => {
        this.navCtrl.push(Step1Page);
    });
    /* Fim da funo de regdirecionamento para a pgina de cadastro */

    /**
     * Funo utilizada para realizar o login do usurio */
    public doLogin = (() => {

        // this.navCtrl.setRoot(SystemTabsPage, {}, {animate: true, direction: 'forward'});

        var data_send = this.logindata;
        this.http.post(this.constants.api_path + 'login/dologin', $.param(data_send)).subscribe(response => {

            var res = (response as any);
            this.login = JSON.parse(res._body);
            const login = (this.login as any);

            if (login.success) {

                this.storage.set('user_logged', login.userdata);

                if (login.no_verified) {
                    console.log(login);
                    this.navCtrl.push(ConfirmCadPage);
                }
                else {
                    this.navCtrl.setRoot(SystemTabsPage, {}, {animate: true, direction: 'forward'});
                }

            } else {
                var __this = this;
                setTimeout(function () {
                    var text = (__this.login as any);
                    text.text = "";
                }, 3000);

            }

        });


    });
    /* Fim da funo de realizao do login do usurio */

    /**
     * Funo para dar inicio ao processo de recuperar senha
     * @type {() => void}
     */
    public recoverPass = ((): void => {
        /**
         * Redireciona o usurio para a primeira pgimna do processo */
        this.navCtrl.push(ForgotPass_1Page);

    });
    /* Fim da funo recoverPass  */

}



